<template>
  <BaseLayout title="售后服务详情">

    <van-steps class="step" :active="step" v-show="step >= 0 && step < 4">
      <van-step v-for="(v,k) in ['申请中', '已审核待收货', '已收货处理中', '已完成']" :key="k">{{v}}</van-step>
    </van-steps>

    <van-notice-bar v-show="state.service.status == 0" left-icon="volume-o" background="#F66" color="#FFF" text="售后申请单已关闭"/>
    <van-notice-bar v-show="state.service.status == 9" left-icon="volume-o" text="售后申请未通过，请修改后重新提交申请。"/>
    <van-cell-group v-show="state.service.status >= 0">
      <van-cell title="申请时间" v-show="state.service.dt_create" :value="state.service.dt_create"></van-cell>
      <van-cell title="审核时间" v-show="state.service.dt_verify" :value="state.service.dt_verify"></van-cell>
      <van-cell title="取件时间" v-show="state.service.dt_pickup" :value="state.service.dt_pickup"></van-cell>
      <van-cell title="返件时间" v-show="state.service.dt_return" :value="state.service.dt_return"></van-cell>
      <van-cell title="退款时间" v-show="state.service.dt_refund" :value="state.service.dt_refund"></van-cell>
      <van-cell title="完成时间" v-show="state.service.dt_finish" :value="state.service.dt_finish"></van-cell>
    </van-cell-group>

    <div class="panel">

      <van-cell class="item" :title="state.item.title" :border="false">
        <template #icon><van-icon :name="state.item.image" :size="72" :style="{marginRight:'10px'}"></van-icon></template>
        <template #extra><div class="num">x {{state.item.num}}</div></template>
      </van-cell>

      <van-cell-group class="group">
        <van-field v-model="state.form[1]" label="服务类型" placeholder="请选择" input-align="right" is-link readonly @click="state.popup=disabled^1;state.index=state.service.type"/>
      </van-cell-group>

      <van-cell-group class="group">
        <van-cell center title="商品状态" label="选中表示包装完整、商品未使用，不影响二次销售">
          <template #right-icon><van-switch v-model="state.service.intact" :disabled="disabled" active-value="1" inactive-value="0" size="22" /></template>
        </van-cell>
        <van-cell center title="申请数量" :label="'最多可申请 ' + state.item.num + ' 件'" v-show="state.item.num > 1">
          <van-stepper v-model="state.service.num" :disabled="disabled" theme="round" button-size="22" :min="1" :max="state.item.num" integer disable-input />
        </van-cell>
        <van-field name="appeal" v-model="state.service.appeal" :readonly="disabled == 1" type="textarea" rows="2" autosize label="申请原因" maxlength="250" placeholder="请描述申请售后服务的具体原因" show-word-limit/>
        <van-field name="attachment" label="补充细节">
          <template #input>
            <van-uploader v-model="list" result-type="file" :disabled="disabled" :after-read="onUpload" :max-count="4" :max-size="1024 * 1024" multiple @delete="true"/>
          </template>
        </van-field>
      </van-cell-group>

      <van-cell-group class="group">
        <van-field v-model="state.form[2]" label="退货方式" placeholder="请选择" input-align="right" is-link readonly @click="state.popup=(disabled^1)*2;state.index=state.service.pickup_type"/>
        <van-field v-model="state.form[3]" type="textarea" rows="3" label="取件地址" placeholder="请选择" input-align="right" is-link readonly v-show="state.service.pickup_type == 1" @click="state.popup=(disabled^1)*3"></van-field>
        <van-field v-model="state.form[4]" type="textarea" rows="3" label="返件地址" placeholder="请选择" input-align="right" is-link readonly v-show="state.service.type > 0"         @click="state.popup=(disabled^1)*4"></van-field>
      </van-cell-group>

      <van-button type="primary" block round v-show="disabled == 0" @click="onSubmit">申请</van-button>

    </div>

    <van-popup v-model:show="state.popup" position="bottom" round>
      <van-picker :columns="state.options[state.popup]" :default-index="state.index" :allow-html="true" @cancel="state.popup = 0" @confirm="onConfirm"/>
    </van-popup>

  </BaseLayout>
</template>

<style scoped>
.step {
  padding: 10px;
}
.num {
  position: absolute;
  right: 10px;
  bottom: 5px;
  color: red;
  font-weight: 600;
}
.panel {
  padding: 5px;
}
.item {
  margin: 10px 0;
  border: solid 3px #FEE;
  border-radius: 5px;
}
.group {
  margin-bottom: 10px;
  border: solid 1px #EEE;
  border-radius: 5px;
}

.contact {
  width: 100%;
}
.address {
  width: 100%;
  color: #CCC;
  font-size: 12px;
}

</style>

<script>
import {reactive, ref} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  props: {
    id: {
      type: [String, Number],
      default: 0,
    },
    oid: {
      type: [String, Number],
      default: 0,
    },
    sku: {
      type: [String, Number],
      default: 0,
    },
  },

  data() {
    const state = reactive({
      item: {},
      order: {},
      service: {
        id: 0,
        type: 0,
        intact: "1",
        num: 1,
        pickup_type: 1,
        status: -1,
      },
      address: [],

      // picker
      popup: 0,
      index: 0,
      options: {
        "1": ["退货退款", "换货", "维修"],
        "2": ["自行快递", "上门取件"],
      },
      form: {},
    });
    const disabled = ref(1);
    const step = ref(-1);
    const list = ref([]);
    return {state, disabled, step, list};
  },

  created() {

    if (_.toSafeInteger(this.id) > 0) {
      this.$http.get("/mall/serviceDetail?id=" + this.id).then((data) => {
        this.state.service = data;
        this.step = data.status - 1;
        this.list = _.map(_.without(data.attachment.split(","), ""), (v) => {return {url:v,isImage:true};});
        if (data.channel > 0 && data.status > 0 && data.status < 4){
          this.$http.get("/" + this.channel[data.channel]["type"] + "/serviceUpdate?id=" + this.id);
        }
        this.onInit(data.oid, data.sku);
      });
    }
    else {
      this.onInit(this.oid, this.sku);
    }

    this.$http.get("/mall/addressList").then((data) => {
      let arr = [];
      for(let i in data) {
        arr.push(this.parseAddress1(data[i]));
      }
      this.state.options[3] = arr;
      this.state.options[4] = arr;
      this.state.address = data;
      this.onLoad();
    });
  },

  computed: {
    ...mapState(["channel"]),
  },

  methods: {
    onInit(oid, sku){
      this.disabled = this.state.service.id > 0 && this.state.service.status < 9;
      this.$http.post("/mall/orderDetail?id=" + oid).then((data) => {
        this.state.order = data;
        this.state.item = _.find(data.detail, {"sku": sku.toString()});
        this.onLoad();
      });
    },
    onLoad(){
      this.state.form[1] = this.state.options[1][this.state.service.type];
      this.state.form[2] = this.state.options[2][this.state.service.pickup_type];
      if (!_.isEmpty(this.state.service.pickup_address)){
        this.state.form[3] = this.parseAddress2(this.state.service.pickup_address);
      }
      if (!_.isEmpty(this.state.service.return_address)){
        this.state.form[4] = this.parseAddress2(this.state.service.return_address);
      }
    },

    onConfirm(text, index){
      this.state.popup == 1 && (this.state.service.type = index);
      this.state.popup == 2 && (this.state.service.pickup_type = index);
      this.state.popup == 3 && (this.state.service.pickup_address = this.state.address[index]);
      this.state.popup == 4 && (this.state.service.return_address = this.state.address[index]);
      this.state.popup = 0;
      this.onLoad();
    },

    onSubmit(){
      if (typeof this.state.service.appeal == "undefined" || this.state.service.appeal.length < 10) {
        return this.$toast("请将“申请原因”填写完整");
      }
      if (this.state.service.pickup_type > 0 && _.isEmpty(this.state.service.pickup_address)) {
        return this.$toast("请选择“取件地址”");
      }
      if (this.state.service.type > 0 && _.isEmpty(this.state.service.return_address)) {
        return this.$toast("请选择“返件地址”");
      }

      let data = _.assign({}, this.state.service);
      if (this.state.service.id == 0) {
        data = _.assign(data, this.state.item, {oid: this.oid, channel: this.state.order.channel});
      }
      data["jdoid"] = this.state.order.extra;
      data["attachment"] = _.reduce(this.list, (r,v) => {r && r.push(v.url);return r;}, []).join(",");
      data["pickup_address"] = JSON.stringify(this.state.service["pickup_address"]);
      data["return_address"] = JSON.stringify(this.state.service["return_address"]);

      this.$http.post("/" + this.channel[data.channel]["type"] + "/serviceApply", data).then((data) => {
        this.$router.push("?id=" + data.id);
      });
    },

    onUpload(file) {
      file.status = 'uploading';
      file.message = '上传中...';
      var data = new FormData();
      data.append('file', file.file);
      this.$http.post("/mall/upload", data).then((data) => {
        file.status = 'done';
        file.message = '上传成功';
        file.url = data.filepath;
      });
    },

    parseAddress1(obj) {
      return "<div style='text-align:center;'>" + obj["name"] + " " + obj["mobile"] + "<div style='color:#999;font-size:12px;'>" + obj["city"] + obj["county"] + obj["address"] + "</div></div>";
    },
    parseAddress2(obj) {
      return obj["name"] + " " + obj["mobile"] + " " + obj["province"] + obj["city"] + obj["county"] + " " + obj["address"];
    },
  }
}
</script>
